import React, { Component } from 'react';
import { connect } from 'react-redux';
import { CSSTransition, TransitionGroup } from 'react-transition-group';

function mapStateToProps(state) {
  const { guitarras } = state;
  return {
    guitarras,
  }
}

class Guitarras extends Component {
  render() {
    return (
      <section id="guitarras" className="guitarras contenedor">
        <h2>Nuestras guitarras</h2>
        {
          this.props.guitarras.map((guitarra, index) => {
            return (
              <article className="guitarra" key={index}>
                {/* para contralar animaciones bien KEY */}
                <TransitionGroup>
                  <CSSTransition
                    classNames="flickerImg"
                    timeout={500}
                    key={guitarra.image}
                  >
                    <img
                      className="guitarra-image"
                      width="350"
                      alt={guitarra.alt}
                      src={guitarra.image}
                    />
                  </CSSTransition>
                </TransitionGroup>
                <TransitionGroup>
                  <CSSTransition
                    classNames="fade"
                    timeout={{ enter: 1000, exit: false }}
                    key={guitarra.name}
                  >
                  <div className="contenedor-guitarra">
                    <h3 className="guitarra-name">{guitarra.name}</h3>
                    <ol>
                      {guitarra.feautures.map((item, index) => {
                        return (
                          <li key={index}>{item}</li>
                        );
                      })}
                    </ol>
                  </div>
                  </CSSTransition>
                </TransitionGroup>
              </article>
            );
          })
        }
      </section>
    );
  }
}


export default connect(mapStateToProps)(Guitarras);
