import React from 'react';
import ReactDOM from 'react-dom';

import './css/invie.css';
import logo from './logo.svg';
import './css/animations.css';
import Invie from './Invie';
import * as serviceWorker from './serviceWorker';
import Cheet from 'cheet.js';
import { createStore } from 'redux';
import { Provider } from 'react-redux';
import initialState from './store';

const easterA = 'https://i.pinimg.com/originals/7a/d4/11/7ad4118a39a4fe7a933f8fcd891a7ae3.jpg';
const easterB = 'https://github.com/LeonidasEsteban/invie-animated/blob/master/src/images/easter-b.png?raw=true';

const easter = {
  isAnimated: 'is-ok',
  menu: [],
  logo,
  guitarras: [
    {
      image: easterA,
      alt: 'Guitarra pink panther',
      name: 'Invie Panther',
      feautures: ['Listo para pinkearte', 'Aire rosa', 'chistes mudos']
    },
    {
      image: easterB,
      alt: 'Guitarra ánime',
      name: 'Invie Ánime',
      feautures: ['Estilo vintage', 'Liviana', 'Inicia tu camino como Rockstar']
    }
  ]
}

Cheet('i n v i e', () => {
  store.dispatch({
    type: update,
    payload: {
      props: easter
    }
  });
});

Cheet('g o b a c k', () => {
  store.dispatch({
    type: update,
    payload: {
      props: initialState
    }
  });
});

const update = 'UPDATE_PROPS';

function reducer(state, action) {
  switch (action.type) {
    case update:
      const newprops = action.payload.props;
      return { ...state, ...newprops }
    default:
      return state; //TODO: El error estuvo aquí <= por el valor por defecto
  }
}



const store = createStore(reducer, initialState);

ReactDOM.render(
  <React.StrictMode>
    <Provider store={store}>
      <Invie />
    </Provider>,
  </React.StrictMode>,
  document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
