import React from 'react';
import { connect } from 'react-redux';
import { CSSTransition, TransitionGroup } from 'react-transition-group';

import Heart from './Heart';

function mapStateToProps(state) {
  console.log(state);
  return {
    logo: state.logo,
    menu: state.menu,
    isAnimated: state.isAnimated
  }
}

class Portada extends React.Component {
  renderHeart(num) {
    const hearts = new Array(num).fill({});
    return (
      hearts.map((heart, index) => {
        const operator = Math.floor((Math.random() * (window.innerWidth - 80))) + 0;
        const operator2 = Math.floor((Math.random() * (10000 - 0))) + 0;
        console.log(operator);
        const style = {
          left: operator + "px",
          animationDelay: operator2 + "ms"
        }
        return (
          <Heart key={index} style={style} />
        );
      })
    );
  }
  render() {
    return (
      <section id="portada" className={`portada background ${this.props.isAnimated}`}>
        <header id="header" className="header contenedor">
          <TransitionGroup>
            <CSSTransition
              classNames="logoWrap"
              timeout={1000}
              key={this.props.logo}
            >
              <figure className="logotipo">
                <img src={this.props.logo} width="186" height="60" alt="Invie logotipo"/>
              </figure>
            </CSSTransition>
          </TransitionGroup>
          <TransitionGroup>
            <CSSTransition
              classNames="showMenu"
              timeout={1000}
              key={this.props.menu}
            >
              <nav className="menu">
                <ul>{ this.props.menu.map((item) => {
                  return (
                    <li><a href={item.href}>{item.title}</a></li>
                  );
                }) }</ul>
              </nav>
            </CSSTransition>
          </TransitionGroup>
        </header>
        <TransitionGroup>
          { !this.props.isAnimated &&
            <CSSTransition
              classNames="animationInOut"
              timeout={{ enter: 800, exit: 800 }}
              key="portada"
            >
                <div className="contenedor">
                  <h1 className="titulo">Guitarras <span>invie</span>sibles</h1>
                  <h3 className="title-a">Sé la estrella de rock que siempre quisiste ser</h3>
                  <a className="button" href="#guitarras">Conoce mas</a>
                </div>
            </CSSTransition>
          }
        </TransitionGroup>
        {
          this.props.isAnimated &&
          this.renderHeart(100)
        }
      </section>
    );
  }
}

export default connect(mapStateToProps)(Portada);
